  flex-wrap: wrap;
}
.time span {
  font-family: "Noto Sans SC";
  font-size: 1.125rem;
  color: #1652F0;
}
.time .subtitle {
  font-family: "Noto Sans SC";
  font-size: 1.125rem;
  color: #1652F0;
  margin-bottom: 0.5em;
  display: inline-block;
  width: 80%;
  margin-top: 0.5rem;
}
.time .row {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
.time .row:nth-child(2) {
