  opacity: 0.5;
  font-size: 2rem;
  font-weight: 300;
  vertical-align: top;
  display: inline-block;
  font-family: "Noto Sans SC";
}
@media only screen and (max-width: 414px) {
  .container {
    min-height: initial;
    min-width: initial;
    width: 100%;
    height: auto;
    padding-bottom: 160px;
  }
  .illustration-cloud {
    display: none;
  }
  .illustration-rocket {
    display: none;
  }
  .hero-content {
    padding: 68px 1rem 0;
  }
  .top-nav {
    position: relative;
  }
  .hero .hero-content .left {
    width: auto;
    height: auto;
    margin: 0 auto;
  }
  .hero .hero-content .left .title {
    padding: 0 0.125rem 0 0.125rem;
    line-height: 60px;
    text-align: center;
  }
  .hero .hero-content .left .strong {
    font-size: 24px;
  }
  .hero .hero-content .right {
    transform: translate(0, 30%);
    background-size: contain;
    width: 100%;
    height: 400px;
    background-position: top left;
  }
  .hero .download {
    margin-top: 2rem;
    align-items: center;
    justify-content: space-around;
  }
  .hero .download .download-btn img {
    height: 48px;
  }
  .menu {
    display: flex;
    flex-basis: 30%;
    width: 100%;
    justify-content: space-around;
  }
  .menu .menu-opt span {
    display: none;
  }
  .text {
    margin-top: 2rem;
    padding: 0 1rem 0 1rem;
  }
  .time .event {
    flex-basis: 100%;
  }
}
